import { useContext, useState } from 'react';
import './Header.css';
import { Link } from 'react-router-dom';
import { motion, AnimatePresence } from "framer-motion";
import { ModeContext } from "../../context";
import moon from "../../icons/moon_4700960.png";
import light from "../../icons/weather_16382131.png";

const Header = () => {
    const { mode, setMode, active, setactive } = useContext(ModeContext);
    const [tof, settof] = useState(false);

    const lis = [
        { title: "About", href: "/" },
        { title: "Projects", href: "/projects" },
        { title: "Services", href: "/services" },
        { title: "Skills", href: "/skills" },
        { title: "Contact", href: "/contact" },
    ];

    const src = mode === "dark" ? light : moon;

    return (
        <>
            <header className='header'>
                <h2>ASSER</h2>

                <ul className='menu' id='menu'>
                    {lis.map((item, i) => (
                        <li key={i}>
                            <Link
                                to={item.href}
                                className={active === item.title ? "activestyle" : ""}
                                onClick={() => setactive(item.title)}
                            >
                                {item.title}
                            </Link>
                        </li>
                    ))}
                </ul>

                <div className='rights'>
                    <button onClick={() =>
                        setMode(prev => prev === "dark" ? "light" : "dark")
                    }>
                        <img src={src} alt="mode" />
                    </button>

                    <button className='mu' onClick={() => settof(true)}>
                        
                    </button>
                </div>
            </header>

            <AnimatePresence>
                {tof && (
                    <motion.div
                        className="fullscreen-menu"
                        initial={{ opacity: 0 }}
                        animate={{ opacity: 1 }}
                        exit={{ opacity: 0 }}
                    >
                        <button className="close-btn " onClick={() => settof(prev => !prev)} style={{ translate: "100px -5px " }} >
                            
                        </button>

                        <motion.ul
                            initial={{ y: 40, opacity: 0 }}
                            animate={{ y: 0, opacity: 1 }}
                            transition={{ delay: .2 }}
                        >
                            {lis.map((item, i) => (
                                <li key={i}>
                                    <Link
                                        to={item.href}
                                        onClick={() => {
                                            setactive(item.title);
                                            settof(false);
                                        }}
                                    >
                                        {item.title}
                                    </Link>
                                </li>
                            ))}
                        </motion.ul>
                    </motion.div>
                )}
            </AnimatePresence>
        </>
    );
};

export default Header;
